import "./styles.css";
import React from "react";
import axios from "axios";
import requests from "./tmdb";
import tmdb from "./axios";
import SearchBar from "./SearchBar";
import Movie from "./Movie";
import RecommendationList from "./RecommendationList";

export default class App extends React.Component {
  state = { movie: null, recommendations: [] };

  componentDidMount = async () => {
    try {
      const response = await axios.get(requests.fetchTrending);
      console.log(response);
      // this.setState({ recommendations: response.data.results });
    } catch (err) {
      console.log(err);
    }
  };
  getRecommendations = async (movieid) => {
    try {
      const response = await axios.get(
        `${requests.fetchRecommendations(movieid)}`
      );
      // console.log(response);
      this.setState({ recommendations: response.data.results });
    } catch (err) {
      console.log(err);
    }
  };

  onSearch = async (term) => {
    try {
      const response = await tmdb.get(`&query=${term}`);
      this.setState({ movie: response.data.results[0] });
      this.getRecommendations(this.state.movie.id);
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    return (
      <div className="App">
        <SearchBar onSearch={this.onSearch} />
        <Movie movie={this.state.movie} />
        <RecommendationList
          recommendations={this.state.recommendations}
          onSearch={this.onSearch}
        />
      </div>
    );
  }
}
