const API_KEY = "4fde825d7d9f0ad24797ab37f5e831c2";

const requests = {
  fetchRecommendations(movie_id) {
    return `https://api.themoviedb.org/3/movie/${movie_id}/recommendations?api_key=${API_KEY}&language=en-US&page=1`;
  },
  fetchMovie: `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&language=en-US&page=1&include_adult=false`,
  fetchTrending: `https://api.themoviedb.org/3/trending/movie/week?api_key=${API_KEY}`
};

export default requests;
