import React from "react";

export default function RecommendationList({ recommendations, onSearch }) {
  const onMovieClick = (movieTitle) => {
    // console.log(movieid);
    onSearch(movieTitle);
    window.scrollTo({ top: 0, behavior: "smooth" });
  };
  const list = recommendations.map((movie) => {
    return (
      <div
        onClick={() => {
          onMovieClick(movie.title || movie.name);
        }}
        style={{ cursor: "pointer" }}
        key={movie.id}
        className="col-lg-3 col-md-6 mb-5"
        name={movie.title}
      >
        <div className="card" style={{ width: "250px" }}>
          <img
            src={`https://image.tmdb.org/t/p/w500${movie?.poster_path}`}
            className="card-img-top "
            alt={movie.title}
          />
          <div className="card-body">
            <h5 className="card-text">{movie.title}</h5>
            <h5 className="card-text">üåª {movie.vote_count}</h5>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="container">
      <h3 className="mb-5 text-center">
        {list.length > 0
          ? "Recommendations ‚¨á"
          : "Sorry No recommendations for this movie. üíî"}
      </h3>
      <div class="container">
        <div class="row">{list}</div>
      </div>
    </div>
  );
}
