import React from "react";

export default class SearchBar extends React.Component {
  state = { term: null };

  onInputChange = (event) => {
    this.setState({ term: event.target.value });
  };

  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.onSearch(this.state.term);
  };
  render() {
    return (
      <div className="container pt-5 pb-3 mb-3">
        <h3
          className=" text-center text-danger"
          style={{ fontFamily: "Playfair Display", fontWeight: "bolder" }}
        >
          Movie? Movie!
        </h3>
        <p className="mb-5 text-center text-secondary">
          Enter the movie you have just watched and get recommendtaions based on
          that
        </p>
        <form onSubmit={this.onFormSubmit}>
          <input
            className="form-control form-control-lg"
            placeholder="Type and Press Enter to Search"
            type="text"
            onChange={this.onInputChange}
            value={this.state.term}
          />
        </form>
      </div>
    );
  }
}
